import {
  REACT_APP_EMRPDH_OKTA_CLIENT_ID,
  REACT_APP_EMRPDH_OKTA_ISSUER,
  REACT_APP_EMRPDH_OKTA_DOMAIN,
} from "./index";
export const getRedirectUri = () => {
  const urlParams = new URLSearchParams(window.location.search);
  const originalUrl = urlParams.get("uwaUrl");

  // If widget environment provides `uwaUrl`, use it
  if (originalUrl) return originalUrl;

  // If running on GitHub Pages
  if (window.location.href.includes("github.io")) {
    return "https://jatin2024.github.io/EMR-PDH/login/callback";
  }

  // Default: assume 3DEXPERIENCE
  return "https://oi000186152-us1-ifwe.3dexperience.3ds.com/api/widget/frame/login/callback";
};
const CLIENT_ID = REACT_APP_EMRPDH_OKTA_CLIENT_ID;
const ISSUER = REACT_APP_EMRPDH_OKTA_ISSUER;
const domain = REACT_APP_EMRPDH_OKTA_DOMAIN;

const OKTA_TESTING_DISABLEHTTPSCHECK =
  process.env.OKTA_TESTING_DISABLEHTTPSCHECK || false;
const REDIRECT_URI = getRedirectUri();
console.log("dfrgthyju", REDIRECT_URI);
// eslint-disable-next-line
export default {
  oidc: {
    clientId: CLIENT_ID,
    issuer: ISSUER,
    redirectUri: REDIRECT_URI,
    scopes: ["openid", "profile", "email"],
    pkce: true,
    disableHttpsCheck: OKTA_TESTING_DISABLEHTTPSCHECK,
    domain: domain,
  },
  resourceServer: {
    messagesUrl: `${window.location.origin}`,
  },
};
